import React, { Component } from "react";
import "../../stylesheets/components/font.css";
import "../../stylesheets/components/homeInfo.css";
import manualPic from "../../images/manual.jpg";
import kkbsLogo from "../../images/kkbs-logo.png";
import { withRouter } from "react-router-dom";

class homeInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
    };
  }

  render() {
    return (
      <div className="all">
        {/* left column */}
        <div className="left-column">
          <div className="about">
            <h1></h1>
            <div className="about-info">
              <p>
                
                SMEs  (Swot Analysis
                ,Organization Life Cycle)
                
                
                
              </p>
            </div>
          </div>
          <div className="home-button">
            <div className="buttons">
              <button
                className="button-swot "
                id="button-swot"
                onClick={() => {
                  this.props.history.push("/swot-info");
                  window.scrollTo(0, 0);
                }}
              >
                 SWOT
              </button>
              <button
                className="button-olc"
                id="button-olc"
                onClick={() => {
                  this.props.history.push("/olc-info");
                  window.scrollTo(0, 0);
                }}
              >
                 OLC
              </button>
              <button
                className="button-both"
                id="button-both"
                onClick={() => {
                  this.props.history.push("/pre-swotolc");
                  window.scrollTo(0, 0);
                }}
              >
                 SWOT  OLC
              </button>
            </div>
          </div>
          {/* <div className="info">
            <div className="swot-info">
              <p>
                <span
                  style={{
                    textAlign: "center",
                    fontSize: "2em",
                    color: "#f16f20",
                    justifyContent: "center",
                  }}
                >
                   Swot Analysis
                  <br />
                </span>
                <br /> 1. 
                 <br />
                <br />
                2. 
                 Swot Analysis  
                 
                
                 Next 
                <br />
                <br /> 3.  5
                 
                 1  5
                
                
                
                
                 Next
                
                <br /> <br />
                4. 
                 BCG Matrix
                
                <br />
              </p>
            </div>
          </div> */}
          {/* <div className="info2">
            <div className="olc-info">
              <p>
                <span
                  style={{
                    textAlign: "center",
                    fontSize: "2em",
                    color: "#f16f20",
                    justifyContent: "center",
                  }}
                >
                   OLC Analysis
                  <br />
                </span>
                <br />
                1.  10
                 <br />
                <br />
                2. 
                
                (OLC)
                <br />
                <br /> 3. 
                
                 5 
                
                1-5  
                <br />
                <br />
                * 1.
                 2.
                
                ( ..2563)
                <br />
              </p>
              
            </div>
          </div> 
          */}
        </div>

        {/* right column */}
        <div className="right-column">
          <div className="news">
            <h1></h1>

            <div className="cards">
              <div className="card1">
                <div className="card1-pic">
                  <img
                    src={manualPic}
                    alt=""
                    onClick={() => {
                      this.props.history.push("/howto");
                      window.scrollTo(0, 0);
                    }}
                    style={{ cursor: "pointer" }}
                  />
                  <h2></h2>
                </div>
              </div>

              <div className="card2">
                <div className="card2-pic">
                  <a href="https://kkbs.kku.ac.th/" target="_blank">
                    <img src={kkbsLogo} alt="" />
                  </a>
                  <h2> website</h2>
                </div>
              </div>
            </div>
          </div>
          {/*facebook page*/}
          <div className="facebook">
            <h1>Facebook page</h1>
            <div
              style={{
                display: "block",
                margin: "0 auto",
                width: "100%",
              }}
            >
              <div
                className="fb-page page1"
                data-href="https://www.facebook.com/IQMKKUModel/"
                data-tabs="timeline"
                data-width="800px"
                data-height=""
                data-small-header="false"
                data-adapt-container-width="true"
                data-hide-cover="false"
                data-show-facepile="true"
              >
                <blockquote
                  cite="https://www.facebook.com/IQMKKUModel/"
                  class="fb-xfbml-parse-ignore"
                >
                  <a href="https://www.facebook.com/IQMKKUModel/">Facebook</a>
                </blockquote>
              </div>

              <div
                className="fb-page page2"
                data-href="https://www.facebook.com/IQMKKUModel/"
                data-tabs="timeline"
                data-width="300px"
                data-height=""
                data-small-header="false"
                data-adapt-container-width="true"
                data-hide-cover="false"
                data-show-facepile="true"
              >
                <blockquote
                  cite="https://www.facebook.com/IQMKKUModel/"
                  class="fb-xfbml-parse-ignore"
                ></blockquote>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(homeInfo);
