import React, { Component } from "react";
import Navbar from "../components/navbar";
import Strength from "../components/strength";
import "../../stylesheets/pages/swotPage.css";
import Footer from "../components/footer";

export default class olcResult extends Component {
  constructor(props) {
    super(props);

    this.state = {
      btnActive: 3,
      data: {
        datasets: [
          {
            backgroundColor: "rgb(241, 111, 32,0.6)",
            borderColor: "rgb(241, 111, 32,1)",
            pointStyle: "circle",
            rotation: 45,
            borderWidth: 10,
            hoverRadius: 10,
            data: [],
          },
        ],
      },
      modeOlc: "",
    };
  }

  // Before render
  componentDidMount = async () => {
    let details = JSON.parse(localStorage.getItem("finalScoreOLC"));
    await localStorage.setItem('isFinished', true)
    console.log("detailllllslslslslssl:", details);
    let phraseResult = details[0].phrase;

    switch (true) {
      case phraseResult === "OLC1":
        await this.setState({
          modeOlc:
            "1.  (Introduction/Birth) :           	",
        });
        break;

      case phraseResult === "OLC2":
        await this.setState({
          modeOlc:
            "2.  (Growth) :   (Growth)            ",
        });
        break;

      case phraseResult === "OLC3":
        await this.setState({
          modeOlc:
            "3.  /  (Maturity) :               	",
        });
        break;

      case phraseResult === "OLC4":
        await this.setState({
          modeOlc:
            "4.  /  (Decline) :                ",
        });
        break;

      case phraseResult === "OLC5":
        await this.setState({
          modeOlc:
            "5.  (Death) :                  ",
        });
        break;
    }
  };

  render() {
    let { btnActive } = this.state;
    return (
      <div>
        <Navbar swot={btnActive} />
        <div style={{ width: "100%", textAlign: "center", marginTop: "5%" }}>
          <h1 style={{ fontFamily: "bold", color: "#363c54", fontSize: "2em" }}>
            OLC
          </h1>
        </div>

        <div
          className="info3"
          style={{ padding: "2%", marginLeft: "2%", marginRight: "2%" }}
        >
          <div className="swotolc-info" tyle={{ margin: "0%" }}>
            <p>
              <p
                style={{
                  fontFamily: "light",
                  color: "#f16f20",

                  textAlign: "left",
                  margin: "0",
                  padding: "0",
                }}
              >
                {this.state.modeOlc}
              </p>
            </p>
            {/*<img src={olcPic} alt="olc-pic" className="olc-pic" />*/}
          </div>
        </div>

        <div
          className="home-button"
          style={{ marginBottom: "5%", paddingLeft: "2%", paddingRight: "2%" }}
        >
          <div className="buttons">
            <button
              className="button-swot "
              id="button-swot"
              onClick={() => {
                this.props.history.push("/swot-info");
                window.scrollTo(0, 0);
              }}
            >
               SWOT
            </button>
            <button
              className="button-olc"
              id="button-olc"
              onClick={() => {
                this.props.history.push("/olc-info");
                window.scrollTo(0, 0);
              }}
            >
               OLC
            </button>
            <button
              className="button-both"
              id="button-both"
              onClick={() => {
                this.props.history.push("/pre-swotolc");
                window.scrollTo(0, 0);
              }}
            >
               SWOT  OLC
            </button>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
